.alert {
  color: #333;
  text-align: center;
}

.alert h3 {
  margin: 0;
  margin: 5px;
}

.alert p {
  overflow: auto;
}

.alert span {
  font-weight: bold;
  overflow-wrap: anywhere;
}

.alert button {
  background-color: rgb(100, 175, 255);;
  border: none;
  color: white;
  padding: 10px;
  font-size: 1em;
  border-radius: 5px;
  cursor: pointer;
  margin: 5px;
}

.alert button:hover {
  transform: scale(1.1);
}
